:root {
  --orange: rgb(227, 136, 38);
  --cyan: rgb(0, 105, 112);
  --dark-cyan: rgb(0, 66, 65);

  --text: rgba(255, 255, 255, 0.75);
  --bg: rgb(242, 242, 242);

  --ff: "Lexend Deca";
  --acent-ff: "Big Shoulders Display";
}

/* lexend-deca-regular - latin */
@font-face {
  font-family: "Lexend Deca";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/lexend-deca-v21-latin-regular.woff2") format("woff2"), url("fonts/lexend-deca-v21-latin-regular.woff") format("woff");
  font-display: swap;
}

/* big-shoulders-display-regular - latin */
@font-face {
  font-display: swap;
  font-family: "Big Shoulders Display";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/big-shoulders-display-v19-latin-regular.woff2") format("woff2"),
    url("fonts/big-shoulders-display-v19-latin-regular.woff") format("woff");
}

/* big-shoulders-display-700 - latin */
@font-face {
  font-display: swap;
  font-family: "Big Shoulders Display";
  font-style: normal;
  font-weight: 700;
  src: url("fonts/big-shoulders-display-v19-latin-700.woff2") format("woff2"), url("fonts/big-shoulders-display-v19-latin-700.woff") format("woff");
}

/*RESET*/
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a,
button {
  cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol,
ul,
menu {
  list-style: none;
}

/* For images to not be able to exceed their container */
img {
  /**/
  display: inline-block;
  max-width: 100%;
  /**/
  max-inline-size: 100%;
  max-block-size: 100%;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input,
textarea {
  -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
  -webkit-appearance: revert;
  appearance: revert;
}

/* preformatted text - use only for this feature */
:where(pre) {
  all: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
  color: unset;
}

/* remove default dot (â€¢) sign */
::marker {
  content: initial;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
  display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable="true"]) {
  -webkit-user-drag: element;
}

/* Revert Modal native behavior */
:where(dialog:modal) {
  all: revert;
}

/**/

body {
  overflow: auto;
  overflow-x: hidden;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: subpixel-antialiased !important;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling: touch;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

/*APP*/

html {
  height: 100%;
  font-family: var(--ff), sans-serif;
  font-size: 15px;
}

body {
  height: 100%;
  min-height: 100vh;
}

.container {
  max-width: 900px;
  padding: 0 1em;
  margin: 0 auto;
}
