.btn {
  padding: 0.95em 2em;
  border-radius: 2em;
  border: 2px solid var(--bg);
  margin-top: auto;
  color: black;
  background-color: var(--bg);
  mix-blend-mode: screen;
  cursor: pointer;
  align-self: flex-start;
}
.btn:hover {
  background-color: transparent;
  color: var(--bg);
}

.btn:disabled {
  opacity: 0.5;
}
