.grid__item {
  display: flex;
  flex-direction: column;
  padding: 2.8em 2.5em;
  color: var(--text);
}

.grid__title {
  margin-bottom: 1em;
  font-family: var(--acent-ff), sans-serif;
  font-weight: 700;
  font-size: 2.2em;
  text-transform: uppercase;
}

.grid__text {
  margin-bottom: clamp(2em, 10vh, 6.5em);
  line-height: 2;
}
